.section--hero-gallery {
	@extend %section--hero;

	.background {
		height: 100%;

		&:before {
			background: #E9E7E3;
		}
	}

	.group--intro {
		text-align: center;
		margin-bottom: 70px;
	}

	.group--back {
		text-align: right;
	}

	.title {
		position: relative;
	}

	.picture {
		margin-bottom: 100px;

		.abstract {
			position: absolute;
			right: 100%;
			top: 0;
			font-size: 11px;
			font-weight: $font-light;

			span {
				display: block;
				position: absolute;
				top: -5px;
				left: -180px;
				right: 65px;
				width: auto;
				text-align: right;
				line-height: 1;
			}

			&:before,
			&:after {
				content: '';
				display: block;
				position: absolute;
			}

			&:before {
				width: 45px;
				height: 2px;
				background: #232120;
				top: -1px;
				left: -53px;
			}

			&:after {
				width: 17px;
				height: 17px;
				border: 2px solid #232120;
				border-radius: 50%;
				box-sizing: border-box;
				top: -8.5px;
				left: -9px;
			}
		}
	}

	@include media-breakpoint-up(sm) {
		padding: 270px 0 0 0;

		.background {
			padding-top: 0;
		}

		.group--back {
			text-align: right;
		}
	}

	@include media-breakpoint-up(lg) {
		.background {
			padding-top: 0;
		}
	}

	@include media-breakpoint-down(xs) {
		padding: 130px 0 0 0;

		.background {
			padding-top: 0;
		}

		.group--back {
			margin-bottom: 60px;
			text-align: left;
		}
	}

}
